<template>
  <div class="edit-memorial__sidebar"  v-scroll-lock="isEditing">
    <div class="memorial-sidebar__content">

      <div :class="['memorial-sidebar__content__header', {'default-block': editBlock === 'default'}]">
        <span class="icon icon__close close" @click="close"></span>
        <h3 v-if="title" v-html="title"></h3>
        <p v-if="title && editBlock !== 'default'" class="quiet" @click="setEditingBlock">
          <span class="icon icon__arrow-2 white icon-return"></span>
            {{ $ml.get('story.sidebar.back') }}
        </p>
      </div>


      <div class="memorial-sidebar__body">
        <slot name="sidebar-content"></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "StorySidebar",
  props: {
    title: {
      type: String,
      default: '',
    },
    editBlock: {
      type: String,
      default: '',
    },
    isEditing: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    close() {
      this.$emit('close');
    },
    setEditingBlock() {
      this.$emit('setEditingBlock');
    },
  },
}
</script>

