<template>
    <!-- qoute qoute--video-player -->
    <div :class="['qoute family__qoute qoute--video-player', ...addClass]" v-if="story.content[0].file">
        <div class="qoute__img-box">
            <div class="qoute__img">
                <div class="qoute__img-wrapper">
                    <router-link v-if="memorialSlug" :to="{ name: 'memorials.relatives', params: { slug: memorialSlug, relative: story.owner.user.id } }">
                        <img :src="story.owner.user.profilePhoto ? story.owner.user.profilePhoto.url : require('@/assets/img/icon/form-human.svg')"
                             :alt="story.owner.user.name">
                    </router-link>
                    <img v-else :src="story.owner.user.profilePhoto ? story.owner.user.profilePhoto.url : require('@/assets/img/icon/form-human.svg')" :alt="story.owner.user.name">
                </div>
                <div class="qoute__name">{{ story.owner.user.name }}</div>
                <div class="qoute__relations">{{ story.owner.relationToMemorialPerson }}</div>
            </div>
        </div>

        <div class="qoute__video-player">
            <video-player :options="videoOptions" :addClass="'flowplayer qoute__video-flowplayer'" :id="'vp-'+story.id"/>
        </div>
        <p class="qoute__text" v-html="story.description">
        </p>
    </div>
</template>

<script>
    import VideoPlayer from "@/components/VideoPlayer.vue";

    export default {
        name: "VideoStory",
        props: [
            'addClass', 'story','memorialSlug'
        ],
        components: {
            VideoPlayer
        },
        data() {
            return {
                videoOptions: {
                    autoplay: false,
                    controls: true,
                    sources: [
                        {
                            src: null,
                        }
                    ]
                }
            }
        },
        created() {
            if(this.story.content[0].file) {
                this.videoOptions.sources[0].src = this.story.content[0].file.url;
            }
        },
    }
</script>