<script>
  import {
    mapGetters
  } from "vuex";
  import Swiper, {
    Navigation
  } from 'swiper';
  Swiper.use([Navigation]);
  import {
    EventBus
  } from '@/utils/EventBus.js';

  import Header from './partials/Header';
  import PersonSidebar from '@/components/stories/sidebars/About';
  import StorySidebar from '@/components/StorySidebar';
  import Button from '@/components/Button';
  import AudioStory from '@/components/stories/AudioStory';
  import TextStory from '@/components/stories/TextStory';
  import VideoStory from '@/components/stories/VideoStory';
  import ImageStory from '@/components/stories/ImageStory';

  export default {
    name: "MemorialsShow",
    components: {
      Header,
      StorySidebar,
      PersonSidebar,
      Button,
      AudioStory,
      TextStory,
      VideoStory,
      ImageStory,
    },
    data() {
      return {
        isEditing: false,
        editBlock: '',
        swiperOptions: {
          direction: 'horizontal',
          loop: false,
          slidesPerView: 3,
          spaceBetween: 20,
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
          },
        },
        lang: 'en',
        platform: 'desktop',
        storiesSlider: null,
      }
    },
    computed: {
      ...mapGetters({
        memorial: 'memorial/getMemorialCreated',
        user: 'profile/getUser',
        countStories: 'dashboard/countStories',
        periods: 'period/getPeriods',
        stories: 'story/getStories',
        isLoggedIn: 'auth/isLoggedIn',
        token: 'auth/token',
      }),
    },
    methods: {
      closeSidebar() {
        this.isEditing = false;
        this.editBlock = '';
      },
      changeLanguage(lang) {
        this.lang = lang;
      },
      changePlatform(platform) {
        this.platform = platform;
      },
      setEditing() {
        this.edit('common');
      },
      edit(block) {
        if (this.isEditing) {
          this.isEditing = false;
          this.editBlock = '';
        } else {
          this.isEditing = true;
          this.editBlock = block;
        }

        EventBus.$emit('memorial-edit', this.isEditing);
      },
      isMemorialFieldExist(field) {
        return typeof this.memorial[field] !== 'undefined' && null !== this.memorial[field];
      },
      initSlider() {
        this.storiesSlider = new Swiper('.swiper-container', this.swiperOptions);
      },
      destroySlider() {
        this.storiesSlider.destroy();
      },
    },
    async mounted() {
      this.initSlider();
    },
    watch: {
      lang(newLang) {
        this.destroySlider(true, true);

        let _this = this;
        setTimeout(() => {
          _this.initSlider();
        }, 100);
      },
    },
    async created() {
      this.$store.commit('memorial/setDefaultData');
      this.lang = this.$ml.current;

      await this.$store.dispatch('memorial/getMemorial', this.$route.params.id);
      await this.$store.dispatch('period/getPeriods', {
        id: this.memorial.id
      });
      await this.$store.dispatch('story/getRandomStories', {
        id: this.memorial.id,
        count: 6
      });

      if (!this.memorial) {
        return this.$router.push({
          name: 'home'
        });
      }
    },
  }
</script>

