<template>
  <div class="memorial-sidebar__details">
    <div class="memorial-sidebar__item">
      <div class="memorial-sidebar__item-wrapper">
        <div class="memorial-sidebar__switcher">
          <div class="memorial-sidebar__switcher-title">{{ $ml.get('editMemorial.sidebar.about.showBlock.label') }}
          </div>
          <div class="memorial-sidebar__switcher-wrapper">


            <div
                :class="['memorial-sidebar__switcher-box', {'switch': !isVisible}]"
                @click="saveAboutVisible"
            >
              <div class="memorial-sidebar__switcher-point"></div>
            </div>
            <span>{{ $ml.get('editMemorial.sidebar.about.showBlock.showBtn') }}</span>
          </div>
        </div>
      </div>
    </div>

    <div class="memorial-sidebar__item">
      <div class="memorial-sidebar__item-wrapper">
        <div class="memorial-sidebar__period-title">
          {{ $ml.with('name', memorial.name).get('editMemorial.sidebar.about.descriptionBlock.label') }}
        </div>
        <div
            class="memorial-sidebar__about-text"
            contenteditable="true"
            inputmode="text"
            v-html="description"
            @blur="saveAboutText"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "About",
  props: {
    memorial: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      description: '',
      isVisible: false,
      isSwitching: false,
    };
  },
  methods: {
    async saveAboutVisible() {
      if (!this.isSwitching) {
        this.isSwitching = true;
        this.isVisible = !this.isVisible;
        const payload = {
          memorialId: this.memorial.id,
          data: {
            'editVersion': this.memorial.editVersion,
            'descriptionVisible': this.isVisible,
          },
        };

        await this.$store.dispatch('dashboard/updateFields', payload);
        this.isSwitching = false;
        this.$emit('updated');
      }
    },
    async saveAboutText(event) {
      this.description = event.target.innerText;
      const payload = {
        memorialId: this.memorial.id,
        data: {
          'editVersion': this.memorial.editVersion,
          'description': this.description,
        },
      };

      await this.$store.dispatch('dashboard/updateFields', payload);
      this.$emit('updated');
    },
  },
  mounted() {
    this.description = this.memorial.description;
    this.isVisible = this.memorial.descriptionVisible;
  },
}
</script>

<style scoped lang="scss">
  @import "~@/assets/scss/components/sidebars/about.scss";
</style>