<template>
    <!-- qoute qoute--common-image -->
    <div :class="['qoute family__qoute qoute--common-image', ...addClass]" v-if="story.content[0].file || story.content[0].link" :data-id="story.id">
        <div class="qoute__img-box">
            <div class="qoute__img">
                <div class="qoute__img-wrapper">
                    <router-link v-if="memorialSlug" :to="{ name: 'memorials.relatives', params: { slug: memorialSlug, relative: story.owner.user.id } }">
                        <img :src="story.owner.user.profilePhoto ? story.owner.user.profilePhoto.url : require('@/assets/img/icon/form-human.svg')" :alt="story.owner.user.name">
                    </router-link>
                    <img v-else :src="story.owner.user.profilePhoto ? story.owner.user.profilePhoto.url : require('@/assets/img/icon/form-human.svg')" :alt="story.owner.user.name">
                </div>
                <div class="qoute__name">{{ story.owner.user.name }}</div>
                <div class="qoute__relations">{{ story.owner.relationToMemorialPerson }}</div>
            </div>
        </div>

        <div class="qoute__common-image" v-if="story.content[0].contentType == 'image'">
            <img :src="story.content[0].file.url" alt="">
        </div>
        <div class="qoute__common-image" v-else-if="story.content[0].type == 'link'">
            <a :href="story.content[0].link" target="_blank" v-if="!checkIsYouTubeLink(story.content[0].link)">
                <img v-if="story.content[0].previewImgLink" :src="story.content[0].previewImgLink" alt="">
            </a>

            <iframe
                v-else-if="checkIsYouTubeLink(story.content[0].link)"
                width="100%" height="auto"
                :src="`https://www.youtube.com/embed/${getVideoId(story.content[0].link)}`"
                frameborder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowfullscreen
            ></iframe>
        </div>
        <p class="qoute__text" v-html="story.description">
        </p>
    </div>
</template>

<script>
import { isYouTubeLink, parseVideoId } from '@/utils/YouTubeHelper';

    export default {
        name: "ImageStory",
        props: [
            'addClass', 'story','memorialSlug'
        ],
        methods: {
          checkIsYouTubeLink(link) {
            return isYouTubeLink(link);
          },
          getVideoId(link) {
            return parseVideoId(link);
          },
        },
    }
</script>